import React from "react";


function Game({ id, name, img, text, href }) {
    return (
        <div href={href} className="game">
            <a href={href}>
                <h1 className="game-title">{name}</h1>
                <img src={img} alt={name} className="images" />
            </a>

            <p className="game-text">{text}</p>
        </div>
    );
}

export default Game;